.container-2 {
  display: flex;  
  flex-wrap: wrap;
}
.card-image {
	display: block;
	min-height: 15rem;
	background: #fff center center no-repeat;
	background-size: cover;
}

.card-image > img {
	display: block;
	width: 100%;
	opacity: 0; 
}

.card-image.is-loaded {
	-webkit-filter: none;
	        filter: none; 
	transition: -webkit-filter 1s; 
	transition: filter 1s; 
	transition: filter 1s, -webkit-filter 1s;
}

.card-list {
	display: block;
	margin: 1rem auto;
	padding: 0;
	font-size: 0;
	text-align: center;
	list-style: none;
}

.card {
	display: inline-block;
	width: 90%;
	max-width: 20rem;
	margin: 1rem;
	text-decoration: none;
	overflow: hidden;
	box-shadow: 0 0 3rem -1rem rgba(0,0,0,0.5);
	transition: transform 0.1s ease-in-out, box-shadow 0.1s;
}

.card:hover {
	transform: scale(1.2);
	box-shadow: 0 0.5em 3rem -1rem rgba(0,0,0,0.5);
}

.card-description {
	display: block;
	padding: 1em 0.5em;
	color: #ffffff;
	text-decoration: none;
  background-color: #cdffe8;
}

.card-description > h2 {
  color:#6aa389;
	margin: 0 0 0.5em;
  font-size: 1rem;
}

.card-description > p {
  color: #82c7a8;
  font-size: 0.8rem;
	margin: 0;
}

:root {
  --backgroundColor: rgba(246, 241, 209);
  --colorShadeA: rgb(106, 163, 137);
  --colorShadeB: rgb(121, 186, 156);
  --colorShadeC: rgb(150, 232, 195);
  --colorShadeD: rgb(187, 232, 211);
  --colorShadeE: rgb(205, 255, 232);
}

.options-container {
  display: grid;
  grid-gap:0,5rem;
  gap:0,5rem;
  grid-auto-flow: dense;
  grid-auto-rows: 3rem;
 grid-template-columns: repeat(auto-fit,minmax(6rem,1fr));
 padding-bottom:10px;
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: 0.6rem;
    color:rgb(106, 163, 137);
    color:var(--colorShadeA);
  font-weight: 700;
  text-transform: uppercase;
  font-family: inherit;
}

button.option-button {
   padding: 1em 2em;
   border: 2px solid rgb(106, 163, 137);
   border: 2px solid var(--colorShadeA);
  border-radius: 1em;
  background: rgb(205, 255, 232);
  background: var(--colorShadeE);
transform-style: preserve-3d;
   transition: all 175ms cubic-bezier(0, 0, 1, 1);
}
button.option-button::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgb(150, 232, 195);
  background: var(--colorShadeC);
  border-radius: inherit;
    box-shadow: 0 0 0 2px rgb(121, 186, 156), 0 0.75em 0 0 rgb(106, 163, 137);
    box-shadow: 0 0 0 2px var(--colorShadeB), 0 0.75em 0 0 var(--colorShadeA);
 transform: translate3d(0, 0.75em, -1em);
     transition: all 175ms cubic-bezier(0, 0, 1, 1);
}


button.option-button:hover {
  background: rgb(187, 232, 211);
  background: var(--colorShadeD);
  transform: translate(0, 0.375em);
}

button.option-button:hover::before {
  transform: translate3d(0, 0.75em, -1em);
}

button.option-button:active {
            transform: translate(0em, 0.75em);
}

button.option-button:active::before {
  transform: translate3d(0, 0, -1em);
  
      box-shadow: 0 0 0 2px rgb(121, 186, 156), 0 0.25em 0 0 rgb(121, 186, 156);
  
      box-shadow: 0 0 0 2px var(--colorShadeB), 0 0.25em 0 0 var(--colorShadeB);

}
 .flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}


.social-links {
	display: grid;
	grid-gap:0.5rem;
	gap:0.5rem;
	grid-auto-flow: dense;
	grid-auto-rows: 1rem;
   grid-template-columns: repeat(auto-fit,minmax(1rem,1fr));
}

.social-btn {
	cursor: pointer;
	height: 50px;
	width: 50px;
	font-family: 'Titillium Web', sans-serif;
	color: #333;
	border-radius: 10px;
	box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
	background: white;
	margin: 5px;
	transition: 1s;
}

.social-btn span {
	width: 0px;
	overflow: hidden;
	transition: 1s;
	text-align: center;
}

.social-btn:hover {
	width: 150px;
	border-radius: 5px;
}

.social-btn:hover span {
	padding: 2px;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

